import React from 'react'
import * as classes from './GraphStore.module.scss'

export default function GraphStore(props) {
console.log(props.seg1.kg);

  return (
    <div className={classes.graph}>
      <div className={classes.graph__1} style={{width: props.seg1.width, maxWidth: '90%'}}>
          <p className={classes.store}>{props.store}</p>
          <p className={classes.number}>{props.seg1.kg}</p>
      </div>
      <div className={classes.graph__2} style={{width: props.seg2.width, maxWidth: '100%'}}>
        <p className={classes.number}>{props.seg2.kg}</p>
      </div>
      <div className={classes.graph__3} style={{width: props.seg3.width, maxWidth: '100%'}}>
        <p className={classes.number}>{props.seg3.kg}</p>
      </div>
      <div className={classes.graph__4} style={{width: props.seg4.width, maxWidth: '100%'}}>
        <p className={classes.number}>{props.seg4.kg}</p>
      </div>
      <div className={classes.graph__5} style={{width: props.seg5.width, maxWidth: '100%'}}>
        <p className={classes.number}>{props.seg5.kg}</p>
      </div>
      <div className={classes.graph__6} style={{width: props.seg6.width, maxWidth: '100%'}}>
        <p className={classes.number}>{props.seg6.kg}</p>
      </div>
      <div className={classes.graph__7} style={{width: props.seg7.width, maxWidth: '100%'}}>
        <p>{props.Kg}</p>
        <p className={classes.number}>{props.seg7.kg}</p>
      </div>
    </div>
  )
}
